export default {
	data() {
		return {
			article: '',
			webview: '',
		};
	},
	onLoad(option) {
		console.log(option)
		if (option.url) {
			this.urlFetch(option.url)
		} else if (option.id) {
			this.getArticle(option.id)
		}
	},
	methods: {
		preImg(img) {
			this.$app.preImg(img)
		},
		urlFetch(url) {





			this.webview = url

		},
		getArticle(id) {
			this.$app.request('article/notice', {
				id
			}, res => {
				try {
					this.article = JSON.parse(res.data.value)
				} catch (e) {
					this.urlFetch(res.data.value)
				}

				uni.setNavigationBarTitle({
					title: res.data.name
				});
			})
		}
	},
}
